.woocommerce {
  .dimensions {
    box-shadow: 0 10px 48px -13px #ababab !important;
    clear: both;
    position: fixed;
    right: 0;
    width: 11%;
    background: #fbf8f7;
    border-radius: 10px 0 0 10px;
    z-index: 10;

    .dimension-wrap {
      text-align: center;
      margin: 10px;
    }

  }
}


